body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.templateDiv {
    height: 500px;
    /*outline: 1px solid red;*/
    margin: 0 30px;

}

.templateTitle {
    font: 30px "Agency FB";
    color: #518F70;
    text-transform: uppercase;
}

.templateText {
    font: 20px Arial, sans-serif;
    color: #518F70;
}
/*--------------------*/
#container{
    min-height: 100vh;
    overflow: hidden;
    display: block;
    position: relative;
    padding-bottom: 88px;
}

.logo {
    width: 139px;
    height: 66px;
    margin: 25px 0 25px 70px;
}

.upperDiv {
    background: #A3E5C4;
}

.bottomDiv {
    width: 100%;
    padding: 10px 70px;
    position: absolute;
    bottom: 0;
    background: #A3E5C4;
}

.midDiv {

}

.bottomDiv > p {
    color: #fff;
    margin: 20px 0;
    font-size: 24px;
    font-family: "Agency FB";
}

input {
    display: block;
    background: lightgray;
    color: #666;
    padding: 20px;
    font: 24px arial, sans-serif;
    border: 1px solid lightgray;
    border-radius: 3px;
    margin: 15px  0px;
}

.welcomeScreen {
    margin: 30px auto;
    width: 400px;
    display: flex;
    flex-direction: column;
    padding: 40px;
    border: 1px solid  #A3E5C4;
    border-radius: 3px;
    align-items: center;
    align-content: center;
    justify-content: center;
    text-align: center;

}

.button{
    padding: 20px;
    font: 24px "Agency FB";
    background: #A3E5C4;
    border: 1px solid #A3E5C4;
    border-radius: 3px;
    text-align: center;
    color: white;
    margin: 10px;
    min-width: 100px;
}


.signUp {
    color: #A3E5C4;
    background: white;
    display: block;
    margin: auto;
}


.wrapper {
    display: flex;
    height: 100vh;
}

.navigation {
    background: #A2AEA8;
    width: 300px;
}

.navigation > ul > li > a {
    text-decoration: none;
    font-family: "Agency FB";
    font-size: 30px;
    color: white;
}

.navigation > ul > li {
    list-style: none;
}
navWrapper {
    background: #000;
}



